.aboutme {
  padding-top: 100px;
}

h1 {
  padding-top: 40px;
  padding-left: 15px;
  color: #ccd6f6;
}

span {
  color: #ccd6f6;
  text-align: center;
}

.aboutme .numbered-heading {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  width: 100%;
  white-space: nowrap;
}

.aboutme .numbered-heading::after {
  content: "";
  display: block;
  position: relative;
  top: 2px;
  width: 100%;
  height: 1px;
  margin-left: 20px;
  background-color: #d100ff;
}

ul {
  display: grid;
  grid-template-columns: repeat(3, minmax(140px, 200px));
  padding: 0px;
  margin: 20px 0px 0px;
  overflow: hidden;
  list-style: none;
  text-align: left;
}

li {
  position: relative;
  margin-bottom: 10px;
  padding-left: 20px;
}

li::before {
  content: "▹";
  position: absolute;
  left: 0px;
  color: #d100ff;
}

.img-container {
  -webkit-align-self: center !important;
          align-self: center !important;
  text-align: center !important;
  position: relative;
  top: 45px;
}

.img-container img {
  width: 90%;
  border-radius: 20px;
  margin-top: 30px;
  max-width: 630px;
}

.presentacion {
  color: white;
  background: linear-gradient(rgba(0, 0, 0, 0.5) 100%, rgba(0, 0, 0, 0.5) 100%),
    url(/static/media/europa.36a99cd7.png);
  background-size: cover;
  background-position: center;

  background-repeat: no-repeat;
  width: 100%;

  text-align: center;

  padding-top: 90px;
  padding-bottom: 90px;
  max-width: -1px;
}

.presentacion span {
  font-size: 18px;
  font-family: "Calibre", "San Francisco", "SF Pro Text", -apple-system,
    system-ui, sans-serif;
}

.contact-list {
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 18px;
}

